/*
Base Rules: The default styles.
*/
body {
  color: #000;
  font-family: "Roboto Light";
  position: relative;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: "Dosis-Bold";
  line-height: 1.2; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

b {
  color: #000;
  font-family: "Roboto";
  font-weight: 500; }

a {
  text-decoration: none; }
  a:hover, a:focus {
    text-decoration: none; }

p {
  margin: 0;
  color: #000;
  font-family: "Roboto Light";
  font-range: 320px 1920px;
  font-size: responsive 16px 18px;
  line-height: 1.8; }
  @media screen and (min-width: 992px) {
    p {
      font-size: 20px; } }

svg {
  height: auto; }

strong {
  font-family: "Roboto"; }

/*
    Module Rules make up the smaller, groupable areas of the site.

    Some examples are callouts, calls-to-action (cta), sidebars, and so on.
    They should be named in a way that is independent of the page context, making
    them reusable throughout the site. This is the point of modularity.
*/
.btn {
  appearance: none;
  display: inline-block;
  vertical-align: middle;
  transition: all .3s ease-in;
  cursor: pointer;
  margin: 0;
  box-shadow: none;
  border: 3px solid transparent;
  border-radius: 25px;
  background-color: transparent;
  padding: 11px 61px;
  height: auto;
  touch-action: manipulation;
  color: #fff;
  font-family: "Dosis-Bold";
  font-range: 320px 1920px;
  font-size: responsive 16px 26px;
  font-weight: bold;
  letter-spacing: .08em;
  line-height: 18px;
  line-height: 1.2;
  text-decoration: none;
  text-transform: uppercase; }
  @media screen and (min-width: 1200px) {
    .btn {
      transition: all .3s ease-in-out;
      padding: 11px 81px; } }
  .btn:focus, .btn:hover {
    color: #fff;
    box-shadow: none; }
  .btn:active, .btn.active {
    box-shadow: none;
    color: #fff; }
  .btn-primary {
    border: 3px solid #000;
    background-color: #000;
    color: #fff; }
    .btn-primary:focus, .btn-primary:hover {
      background-color: #fff;
      border: 3px solid #fff; }
  .btn-yellow {
    border: 3px solid #fff;
    background: #000;
    color: #fff; }
    .btn-yellow:focus, .btn-yellow:hover {
      border: 3px solid #000; }

.hiring-list .popup {
  display: none; }
  @media screen and (min-width: 992px) {
    .hiring-list .popup {
      position: absolute;
      top: 0;
      right: 50%;
      transform: translateX(50%);
      width: 200px;
      opacity: 0;
      transition: all 0.5s ease-in-out;
      background: #fff;
      box-shadow: 0 0 49px rgba(1, 1, 1, 0.07);
      padding: 48px 53px 43px;
      cursor: pointer;
      visibility: hidden;
      display: block; } }
  @media screen and (min-width: 992px) {
    .hiring-list .popup {
      top: -230%;
      right: -130%;
      width: 200px;
      width: 378px;
      transform: translateX(0); } }
  .hiring-list .popup.active {
    opacity: 1;
    visibility: visible; }
  .hiring-list .popup__heading {
    font-range: 320px 1920px;
    font-size: responsive 20px 30px;
    text-transform: uppercase; }
  .hiring-list .popup__underline {
    margin: 20px auto;
    max-width: 175px;
    height: 1px;
    background-color: #d1d3d4; }
  .hiring-list .popup__body {
    color: #000;
    line-height: 27px;
    font-size: 18px;
    font-weight: 300;
    line-height: 27px; }
  .hiring-list .popup::before {
    content: '';
    position: absolute;
    left: -5px;
    top: 50%;
    transform: translate(-50%);
    width: 0;
    height: 0;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-right: 15px solid #fff; }

.hiring-list a.underline {
  color: #000;
  line-height: 27px;
  font-size: 18px; }

.hiring-list .trigger {
  position: relative; }

